.scroll-bar{
    //
    &::-webkit-scrollbar-track {
      background-color: map-get($theme-colors, "white");
    }
    //
    &::-webkit-scrollbar {
      width: 2px;
      background-color: black;
    }
    //
    &::-webkit-scrollbar-thumb {
      background-color: map-get($theme-colors, "secondary");
    }

}